section {
  color: #62646a;
}
.about {
  margin-bottom: 70px;
}
.skills {
  margin-top: 30px;
}

.about-title {
  margin-bottom: 20px;
  letter-spacing: 2px;
}

.about-content {
  word-spacing: 5px;
  line-height: 1.3;
}

.skills > ul {
  list-style: none;
}

.skills-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.skills-list > li {
  display: block;
  margin-bottom: 10px;
  text-align: center;
}

.image {
  margin: 40px auto auto auto;
  text-align: center;
  position: relative;
  width: 70%;
}

.image > img {
  margin: auto;
  height: 200px;
  width: 100%;
  object-fit: cover;
  object-position: center center;
  border-radius: 5px;
  display: block;
}

.image::before {
  content: "";
  height: 200px;
  width: 100%;
  border: 2px solid #1dbf73;
  display: block;
  position: absolute;
  left: 10px;
  top: 10px;
  z-index: -1;
  border-radius: 5px;
}
.image > div {
  display: block;
  width: 100%;
  height: 200px;
  top: 0;
  right: 0;
  position: absolute;
  background-color: rgba(52, 255, 162, 0.2);
  border-radius: 5px;
  transition: 0.2s ease-in;
}

.image:hover div {
  background-color: rgba(52, 255, 162, 0);
}

.image:hover::before {
  left: 8px;
  top: 8px;
}
