.contact {
  padding: 90px 0;
}

.contact > div {
  text-align: center;
  /* line-height: 2; */
}

.contact-container > h4 {
  margin-bottom: 12px;
  color: #1dbf73;
}

.contact-container > h2 {
  margin-bottom: 12px;
  /* color: #1dbf73; */
}

.contact-container > p {
  line-height: 1.5;
  word-spacing: 5px;
  text-align: center;
}

.contact-container > a {
  display: block;
  text-decoration: none;
  color: #1dbf73;
  border: 2px solid #1dbf73;
  width: clamp(100px, 35vw, 180px);
  margin: 40px auto 0 auto;
  padding: 15px 15px;
  border-radius: 5px;
}
