@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");

.navigation {
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  height: 40px;

  /* padding: 0 20px; */
}

.mobile-menu {
  position: relative;
  height: 18px;
  width: 30px;
}

.logo > h2 {
  font-weight: bold;
  color: #1dbf73;
  font-size: 23px;
}

.logo {
  position: relative;
  width: 160px;
}

.logo::after {
  content: "";
  height: 28px;
  width: 3px;
  background-color: #1dbf73;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  animation-name: blink;
  animation-duration: 0.7s;
  animation-iteration-count: infinite;
  /* visibility: hidden; */
}

.mobile-menu > .bar-one {
  height: 3px;
  width: 25px;
  background-color: #1dbf73;
  margin-bottom: 2px;
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 1px;
  transition: all 0.2s;
}

.mobile-menu > .bar-two {
  height: 3px;
  width: 20px;
  background-color: #1dbf73;
  margin-bottom: 2px;
  position: absolute;
  top: 5px;
  right: 0;
  border-radius: 1px;
}

.mobile-menu > .bar-three {
  height: 3px;
  width: 15px;
  background-color: #1dbf73;
  position: absolute;
  right: 0;
  top: 10px;
  border-radius: 1px;
  transition: all 0.2s;
}

.mobile-navigation {
  position: absolute;
  border-bottom: 2px solid #1dbf73;
  width: 100vw;
  top: 80px;
  left: 0;
  text-align: center;
  background-color: white;
  transform: scale(0);
  transform-origin: top;
  transition: transform 0.2s;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  box-shadow: 0 2px 2px 0 #62646a;
}

.mobile-navigation > .nav-links {
  list-style: none;
  text-decoration: none;
}

.nav-links a {
  text-decoration: none;
  color: #62646a;
  font-weight: 700;
}
.nav-links {
  line-height: 2;
  padding: 10px 0;
}

@keyframes blink {
  from {
    background-color: #1dbf73;
  }
  to {
    background-color: white;
  }
}
