h1 {
  font-size: clamp(30px, 8vw, 40px);
}

.hero-container {
  /* margin-top: 55px; */
  padding: 50px 0;
  min-height: 100vh;
}

.hero-heading {
  line-height: 1.4;
  /* word-spacing: 5px; */
}

.hero-heading > p {
  word-spacing: 6px;
  color: #1dbf73;
  font-weight: 600;
  margin-bottom: 10px;
  font-size: clamp(16px, 5vw, 20px);
}

.hero-heading > .second-heading {
  color: #999;
  width: 80%;
}

.hero-content {
  margin-top: 20px;
  color: #62646a;
  word-spacing: 5px;
  font-size: clamp(16px, 5vw, 20px);
}

.hero-button {
  margin-top: 50px;
  text-align: center;
  border: 1px solid #1dbf73;
  border-radius: 5px;
  width: 140px;
  padding: 18px;
  font-size: 14px;
  font-weight: 600;
  color: #1dbf73;
}

.hero-button > a {
  text-decoration: none;
  color: #1dbf73;
}

@media screen and (max-width: 420px) {
  .hero-container {
    /* margin-top: 100px; */
    padding: 100px 0;
  }
}

@media screen and (max-width: 330px) {
  .hero-container {
    margin-top: 50px;
    padding: 0;
  }
}
