@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
.navigation {
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  height: 40px;

  /* padding: 0 20px; */
}

.mobile-menu {
  position: relative;
  height: 18px;
  width: 30px;
}

.logo > h2 {
  font-weight: bold;
  color: #1dbf73;
  font-size: 23px;
}

.logo {
  position: relative;
  width: 160px;
}

.logo::after {
  content: "";
  height: 28px;
  width: 3px;
  background-color: #1dbf73;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  -webkit-animation-name: blink;
          animation-name: blink;
  -webkit-animation-duration: 0.7s;
          animation-duration: 0.7s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  /* visibility: hidden; */
}

.mobile-menu > .bar-one {
  height: 3px;
  width: 25px;
  background-color: #1dbf73;
  margin-bottom: 2px;
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 1px;
  transition: all 0.2s;
}

.mobile-menu > .bar-two {
  height: 3px;
  width: 20px;
  background-color: #1dbf73;
  margin-bottom: 2px;
  position: absolute;
  top: 5px;
  right: 0;
  border-radius: 1px;
}

.mobile-menu > .bar-three {
  height: 3px;
  width: 15px;
  background-color: #1dbf73;
  position: absolute;
  right: 0;
  top: 10px;
  border-radius: 1px;
  transition: all 0.2s;
}

.mobile-navigation {
  position: absolute;
  border-bottom: 2px solid #1dbf73;
  width: 100vw;
  top: 80px;
  left: 0;
  text-align: center;
  background-color: white;
  transform: scale(0);
  transform-origin: top;
  transition: transform 0.2s;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  box-shadow: 0 2px 2px 0 #62646a;
}

.mobile-navigation > .nav-links {
  list-style: none;
  text-decoration: none;
}

.nav-links a {
  text-decoration: none;
  color: #62646a;
  font-weight: 700;
}
.nav-links {
  line-height: 2;
  padding: 10px 0;
}

@-webkit-keyframes blink {
  from {
    background-color: #1dbf73;
  }
  to {
    background-color: white;
  }
}

@keyframes blink {
  from {
    background-color: #1dbf73;
  }
  to {
    background-color: white;
  }
}

header {
  margin-top: 30px;
}

h1 {
  font-size: clamp(30px, 8vw, 40px);
}

.hero-container {
  /* margin-top: 55px; */
  padding: 50px 0;
  min-height: 100vh;
}

.hero-heading {
  line-height: 1.4;
  /* word-spacing: 5px; */
}

.hero-heading > p {
  word-spacing: 6px;
  color: #1dbf73;
  font-weight: 600;
  margin-bottom: 10px;
  font-size: clamp(16px, 5vw, 20px);
}

.hero-heading > .second-heading {
  color: #999;
  width: 80%;
}

.hero-content {
  margin-top: 20px;
  color: #62646a;
  word-spacing: 5px;
  font-size: clamp(16px, 5vw, 20px);
}

.hero-button {
  margin-top: 50px;
  text-align: center;
  border: 1px solid #1dbf73;
  border-radius: 5px;
  width: 140px;
  padding: 18px;
  font-size: 14px;
  font-weight: 600;
  color: #1dbf73;
}

.hero-button > a {
  text-decoration: none;
  color: #1dbf73;
}

@media screen and (max-width: 420px) {
  .hero-container {
    /* margin-top: 100px; */
    padding: 100px 0;
  }
}

@media screen and (max-width: 330px) {
  .hero-container {
    margin-top: 50px;
    padding: 0;
  }
}

section {
  color: #62646a;
}
.about {
  margin-bottom: 70px;
}
.skills {
  margin-top: 30px;
}

.about-title {
  margin-bottom: 20px;
  letter-spacing: 2px;
}

.about-content {
  word-spacing: 5px;
  line-height: 1.3;
}

.skills > ul {
  list-style: none;
}

.skills-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.skills-list > li {
  display: block;
  margin-bottom: 10px;
  text-align: center;
}

.image {
  margin: 40px auto auto auto;
  text-align: center;
  position: relative;
  width: 70%;
}

.image > img {
  margin: auto;
  height: 200px;
  width: 100%;
  object-fit: cover;
  object-position: center center;
  border-radius: 5px;
  display: block;
}

.image::before {
  content: "";
  height: 200px;
  width: 100%;
  border: 2px solid #1dbf73;
  display: block;
  position: absolute;
  left: 10px;
  top: 10px;
  z-index: -1;
  border-radius: 5px;
}
.image > div {
  display: block;
  width: 100%;
  height: 200px;
  top: 0;
  right: 0;
  position: absolute;
  background-color: rgba(52, 255, 162, 0.2);
  border-radius: 5px;
  transition: 0.2s ease-in;
}

.image:hover div {
  background-color: rgba(52, 255, 162, 0);
}

.image:hover::before {
  left: 8px;
  top: 8px;
}
