.card {
  box-shadow: -1px 1px 8px 3px rgba(0, 0, 0, 0.2);
  width: 95%;
  margin: 50px auto 30px auto;
  border-radius: 5px;
  border-bottom: 5px solid #1dbf73;
}

.card-image > img {
  object-fit: cover;
  object-position: center center;
  width: 100%;
  display: block;
  height: 300px;
  margin: auto;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.card-data {
  padding: 10px;
}

.card-title,
.card-description,
.card-footer {
  width: 95%;
  margin: 5px auto 0 auto;
  padding: 5px 0;
}
.card-title > p {
  margin-bottom: 3px;
  color: #1dbf73;
}

.card-description {
  line-height: 1.5;
  word-spacing: 3px;
  letter-spacing: 0.6px;
}

.icons {
  display: flex;
  justify-content: flex-end;
  list-style: none;
}

.icons > li {
  margin-right: 5px;
  color: black;
}

.contact {
  padding: 90px 0;
}

.contact > div {
  text-align: center;
  /* line-height: 2; */
}

.contact-container > h4 {
  margin-bottom: 12px;
  color: #1dbf73;
}

.contact-container > h2 {
  margin-bottom: 12px;
  /* color: #1dbf73; */
}

.contact-container > p {
  line-height: 1.5;
  word-spacing: 5px;
  text-align: center;
}

.contact-container > a {
  display: block;
  text-decoration: none;
  color: #1dbf73;
  border: 2px solid #1dbf73;
  width: clamp(100px, 35vw, 180px);
  margin: 40px auto 0 auto;
  padding: 15px 15px;
  border-radius: 5px;
}

.footer-icons > ul {
  list-style: none;
  color: green;
  display: flex;
  justify-content: space-around;
}

.icon-link {
  color: #1dbf73;
}

.icon-link > i {
  width: 20px;
  size: 30px;
}

.footer-content {
  margin: 20px 0;
  text-align: center;
}

#root {
  width: 90%;
  margin: auto;
  scroll-behavior: smooth;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

